.card{
    margin: 20px;
    border-radius: 10px;
    overflow: hidden;
    box-shadow: 0px 6px 18px -9px rgba(0, 0, 0, 0.75);
    transition: transform 100ms ease-in;
    cursor: pointer;
    display: flex;
}
.card:hover{
    transform: scale(1.07);
}
.card__header{
    position: relative;
}
.card__header > img{
    object-fit: fill;
    width: 100%;
}

.card__info{
    margin-top: -9px;
    border-radius: 10px;
    padding: 20px;
    border: 1px;
}
.card__info > h2{
    font-size: 18px;
    font-weight: 600;
}
.card__info > h4{
    font-weight: 300;
    font-size: 14px;
    margin-top: 8px;
    margin-bottom: 8px;
}
.card__info > h3{
    font-weight: 350;
    font-size: 14px;
    margin-top: 8px;
    margin-bottom: 8px;
}
.card__link{
    text-decoration: none !important;
    color: black !important;
}
